.news--wrapper {
	position: relative;
	margin-bottom: 25px;
}

.news {
	position: relative;
	display: flex;
	border: 3px solid #928a83ad;
	background-color: rgb(24 24 25 / 80%);
	clip-path: polygon(1% 1%, 100% 0, 100% 99.5%, 0 99%);
}

.news-cover {
	flex: 1 0 auto;
	width: 250px;
	height: 250px;
}

.news-cover .cover {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.news-content {
	padding: 20px;
}

.news-content .header {
	margin-bottom: 20px;
	font-size: 20px;
}

.news-content .description {
	display: -webkit-box;
	margin-bottom: 25px;
	font-size: 17px;
	overflow: hidden;
	text-overflow: ellipsis;
	-webkit-line-clamp: 3;
	-webkit-box-orient: vertical;

}

.read-more {
	display: block;
	position: absolute;
	right: 25px;
	bottom: -25px;
	padding: 15px 25px;
	font-weight: bold;
	background-color: var(--main-orange);
	text-transform: uppercase;
	color: #000;
}

@media (max-width: 768px) {
    .news-cover {
        width: 200px;
    }

    .news-content .header {
        font-size: 25px;
    }
    
    .news-content .description {
        font-size: 18px;
    }
	
}

@media (max-width: 576px) {

    .news {
        flex-direction: column;
        min-width: 200px;
    }

    .news-cover {
        width: 100%;
    }

	

}