.presentation-section {
    padding: 40px 0;
}

.presentation-section .section-content {
    display: flex;
    height: 660px;
    overflow: hidden;
}

.presentation-section .section-content .image-block {
    max-width: 700px;
    width: 100%;
    height: 100%;
}

.presentation-section .section-content .image-block img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.presentation-section .section-content .text-block {
    position: relative;
    width: 100%;
    margin-left: 50px;
    padding: 50px 0;
    background: url('./../../../assets/title-bg-orange.jpg');
    background-size: cover;
    font-size: 22px;
}

.presentation-section .section-content .text-block::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    background-color: rgb(0 0 0 / 85%);
}

.presentation-section .section-content .text-block p {
    position: relative;
    z-index: 2;
    margin: 0 auto;
    max-width: 700px;
    width: 100%;
    line-height: 1.8;
}

@media (max-width: 1200px) {

	.presentation-section .section-content .text-block {
        font-size: 18px;
    }

}

@media (max-width: 768px) {

    .presentation-section .section-content {
        height: auto;
    }

    .presentation-section .section-content .image-block {
        display: none;
    }

    .presentation-section .section-content .text-block {
        margin-left: 0;
        padding: 50px;
        font-size: 22px;
    }

    .presentation-section .section-content .text-block::before {
        background-color: rgb(0 0 0 / 50%);
    }
	

}