.contact-form {
	display: flex;
	flex-wrap: wrap;
	width: 500px;
	padding: 20px;
	background-color: #fff;
	color: #000;
}

.form-field {
	width: calc(50% - 20px);
	margin: 0 10px 15px 10px;
}

.form-field--wide {
	width: 100%;
	margin: 0 10px 15px 10px;
}

.form-field-container {
	height: 60px;
}

.form-field-container > *{
	width: 100%;
	height: 100%;
	padding: 3px 2px 3px 15px;
	font-family: "Playfair Display", serif;
	font-size: 16px;
}

.form-field-container--message {
	height: 160px;
}

.form-field--title {
	display: block;
	margin-bottom: 10px;
}

.form-field--input {
	display: block;
	border: 1px solid #000;
}

.form-field--message {
	resize: none;
}

.form-submit {
	border: 1px solid transparent;
	background-color: var(--main-orange);
	color: #000;
	cursor: pointer;
}

.form-submit:hover {
	background-color: #fff;
	border-color: #000;
}

@media (max-width: 768px) {
	.contact-form {
		width: 100%;
	}

	.form-field {
		width: 100%;
	}

	.form-field-container--message {
		height: 160px;
	}
}