.sidebar-item {
    position: relative;
    width: 300px;
    height: 250px;
    margin-bottom: 20px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    background-color: #212121;
}

.sidebar-item::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    background-color: rgb(0 0 0 / 30%);
}

.sidebar-item .link {
    position: absolute;
    left: 20px;
    bottom: 20px;
    z-index: 2;
}

.sidebar-item .link:hover {
    text-decoration: underline;
}
