.blog-section {
	position: relative;
	padding-top: 120px;
	padding-bottom: 60px;
	background: url('./../../assets/blogPage-bg.jpg');
	background-size: cover;
	background-repeat: no-repeat;
	background-attachment: fixed;
}

.blog-section::after {
	content: '';
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    background-color: rgb(0 0 0 / 85%)
}

.blog-section .container--inner {
	z-index: 2;
}

.blog-section .news--wrapper {
	margin-bottom: 50px;
}

@media (max-width: 768px) {
	
	.blog-section .container--inner {
		padding: 0 20px;
	}
}

