

.our-games-section .section-text {
	padding-top: 70px;
}



.our-games-showcase--background {
	position: relative;
	padding: 80px 0;
	background: url('./../../../assets/bg-sample.jpg');
	background-size: cover;
    background-repeat: no-repeat;
	background-attachment: fixed;
}

.our-games-showcase--background::after {
	content: '';
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    background-color: rgb(0 0 0 / 10%)
}

.our-games-showcase--background .container {
	z-index: 2;
}

.our-games-showcase {
	display: flex;
	flex-wrap: wrap;
}

.our-games-showcase .game-item--wrapper {
	width: 50%;
	margin-bottom: 300px; 
}

.our-games-showcase .game-item--wrapper:nth-last-child(-n+2) {
	margin-bottom: 0;
}

.our-games-showcase .game-item--wrapper:nth-child(even) .game-item-block {
	margin-left: auto;
}

@media (max-width: 1080px) {

	.our-games-showcase {
		flex-direction: column;
		align-items: center;
	}

	.our-games-showcase .game-item--wrapper {
		width: auto;
		margin-bottom: 0;
	}

	.our-games-showcase .game-item--wrapper .placeholder {
		display: none;
	}



	.demo--main-image {
		width: 100%;
	}

}

@media (max-width: 576px) {

	

	.our-games-showcase--background {
		padding: 0;
	}	

	.our-games-showcase--background .container {
		padding: 0;
	}

	.our-games-showcase .game-item--wrapper {
		width: 100%;
		margin-bottom: 0;
	}

}